// src/components/ChatbotFloating.jsx
import React, { useEffect, useRef, useState } from 'react';
import {
  Box,
  Fab,
  Paper,
  Typography,
  IconButton,
  TextField,
  Button,
  InputAdornment,
  useTheme,
  useMediaQuery,
} from '@mui/material';
import { ChatBubble, Send, Close } from '@mui/icons-material';

/**
 * ChatbotFloating (mejorado)
 * - Modal flotante (Paper) responsive
 * - Streaming de texto (simula typing real-time)
 * - FAB se oculta al abrir
 * - Ms altura (responsive)
 * - Jhania responde EN NOMBRE DE Jhonatan (las respuestas mencionan a Jhonatan)
 */

const KB = [
  { pattern: /\b(edad|aos)\b/i, response: 'Jhonatan tiene 22 aos.' },
  { pattern: /\b(donde|dnde)\b.*\b(vives|vive)\b/i, response: 'Jhonatan vive en Lima, Per.' },
  { pattern: /\b(habilidades|skills|habilidad)\b/i, response: 'Jhonatan se especializa en desarrollo web (React/Node), ML bsico y cloud (AWS/GCP/Azure).' },
  { pattern: /\b(experienc|experiencia|trabaj)\b/i, response: 'Jhonatan ha trabajado como practicante en SUNAT y en soporte en Food Retail S.A.C; adems tiene proyectos pblicos en GitHub.' },
  { pattern: /\b(github|proyecto|proyectos)\b/i, response: 'Los proyectos de Jhonatan estn en https://github.com/Papiriqui' },
  { pattern: /\b(contacto|email|correo|mail)\b/i, response: 'Puedes contactar a Jhonatan en: jhonatan.h.salazar@gmail.com' },
  { pattern: /\b(contrat|trabaj|oferta)\b/i, response: 'Si quieres contratar a Jhonatan, usa el botn "Contrtame" o envale un mail con tu propuesta.' },
];

function findKBReply(text) {
  if (!text) return null;
  for (const item of KB) {
    if (item.pattern.test(text)) return item.response;
  }
  if (/\b(nombre|cmo te llamas|como te llamas)\b/i.test(text)) {
    return 'Soy Jhania, la asistente virtual que responde en nombre de Jhonatan.';
  }
  return "Lo siento, no entend bien. Si quieres, puedes contactar directamente a Jhonatan por correo o preguntar de otra forma.";
}

export default function ChatbotFloating() {
  const theme = useTheme();
  const isSm = useMediaQuery(theme.breakpoints.down('sm'));

  const [open, setOpen] = useState(false);
  const seedMessage = {
    from: 'bot',
    text: 'Hola   Soy Jhania, la asistente virtual que responde en nombre de Jhonatan. En qu puedo ayudarte hoy?',
    ts: Date.now(),
  };

  const [messages, setMessages] = useState(() => {
    try {
      const raw = localStorage.getItem('chat-history-v2');
      return raw ? JSON.parse(raw) : [seedMessage];
    } catch {
      return [seedMessage];
    }
  });

  const [input, setInput] = useState('');
  const [botTyping, setBotTyping] = useState(false);
  const [streamingText, setStreamingText] = useState('');
  const scrollRef = useRef(null);
  const streamingRef = useRef(null);
  const inputRef = useRef(null);

  // persist + autoscroll when messages change
  useEffect(() => {
    try { localStorage.setItem('chat-history-v2', JSON.stringify(messages)); } catch {}
    if (scrollRef.current) {
      // small timeout to ensure DOM updated
      setTimeout(() => {
        scrollRef.current.scrollTop = scrollRef.current.scrollHeight + 200;
      }, 50);
    }
  }, [messages]);

  // cleanup streaming on unmount
  useEffect(() => {
    return () => {
      if (streamingRef.current) clearInterval(streamingRef.current);
    };
  }, []);

  // focus input when open
  useEffect(() => {
    if (open) {
      // focus after modal animation
      setTimeout(() => {
        inputRef.current?.focus();
      }, 140);
      // attach ESC handler
      const onKey = (e) => {
        if (e.key === 'Escape') setOpen(false);
      };
      window.addEventListener('keydown', onKey);
      return () => window.removeEventListener('keydown', onKey);
    }
  }, [open]);

  const send = (text) => {
    if (!text || !text.trim()) return;
    const trimmed = text.trim();
    const userMsg = { from: 'user', text: trimmed, ts: Date.now() };
    setMessages((m) => [...m, userMsg]);
    setInput('');
    // disable input briefly? we'll allow but show typing
    setBotTyping(true);

    // compute bot response (KB)
    const fullReply = findKBReply(trimmed);

    // simulate network latency then stream
    const latency = 350 + Math.random() * 650;
    setTimeout(() => {
      streamBotReply(fullReply);
    }, latency);
  };

  const streamBotReply = (fullReply) => {
    if (!fullReply) {
      // fallback
      fullReply = "Lo siento, no entend. Puedes escribirme de otra forma o contactar a Jhonatan directamente.";
    }
    let i = 0;
    setStreamingText('');
    if (streamingRef.current) clearInterval(streamingRef.current);

    const speed = 14 + Math.floor(Math.random() * 22); // ms per char
    streamingRef.current = setInterval(() => {
      i += 1;
      setStreamingText(fullReply.slice(0, i));
      if (scrollRef.current) scrollRef.current.scrollTop = scrollRef.current.scrollHeight + 200;
      if (i >= fullReply.length) {
        clearInterval(streamingRef.current);
        streamingRef.current = null;
        setMessages((m) => [...m, { from: 'bot', text: fullReply, ts: Date.now() }]);
        setBotTyping(false);
        setStreamingText('');
      }
    }, speed);
  };

  const quickAsk = (q) => {
    setOpen(true);
    setTimeout(() => send(q), 200);
  };

  const clearChat = () => {
    const seed = [seedMessage];
    setMessages(seed);
    try { localStorage.setItem('chat-history-v2', JSON.stringify(seed)); } catch {}
  };

  // responsive height: mobile uses vh, desktop fixed px
  const chatHeight = isSm ? '60vh' : 420;

  return (
    <>
      <style>{`
        @keyframes dots {
          0% { transform: translateX(0); }
          50% { transform: translateX(4px); }
          100% { transform: translateX(0); }
        }
        .chatbot-fab-label {
          display: inline-flex;
          align-items: center;
          gap: 8px;
          font-weight: 600;
        }
        .chatbot-dots span {
          display: inline-block;
          width: 6px;
          height: 6px;
          border-radius: 50%;
          background: white;
          opacity: 0.95;
          margin-left: 3px;
        }
        .chatbot-dots span:nth-child(1) { animation: dots 1s infinite ease-in-out; animation-delay: 0s; }
        .chatbot-dots span:nth-child(2) { animation: dots 1s infinite ease-in-out; animation-delay: 0.12s; }
        .chatbot-dots span:nth-child(3) { animation: dots 1s infinite ease-in-out; animation-delay: 0.24s; }
      `}</style>

      {/* Floating FAB (hidden while open) */}
      {!open && (
        <Box sx={{ position: 'fixed', left: 16, bottom: 20, zIndex: 1400, display: 'flex', alignItems: 'center', gap: 1 }}>
          <Fab
            color="secondary"
            aria-label="Abrir chat"
            onClick={() => setOpen(true)}
            sx={{ boxShadow: 6 }}
          >
            <ChatBubble />
          </Fab>

          {/* label with animated dots (hidden on xs) */}
          <Box
            onClick={() => setOpen(true)}
            sx={{
              bgcolor: 'secondary.main',
              color: 'secondary.contrastText',
              px: 2,
              py: 0.7,
              borderRadius: 4,
              boxShadow: 6,
              cursor: 'pointer',
              display: { xs: 'none', sm: 'inline-flex' },
              alignItems: 'center',
            }}
            aria-hidden
          >
            <span className="chatbot-fab-label">
              Pregntame
              <span className="chatbot-dots" aria-hidden>
                <span />
                <span />
                <span />
              </span>
            </span>
          </Box>
        </Box>
      )}

      {/* Modal flotante bottom-left */}
      {open && (
        <Box
          sx={{
            position: 'fixed',
            left: isSm ? 12 : 16,
            bottom: isSm ? 12 : 90,
            zIndex: 1500,
            width: isSm ? 'calc(100% - 24px)' : 380,
            transition: 'transform 220ms ease, opacity 200ms',
          }}
          role="dialog"
          aria-label="Chat con Jhania"
        >
          <Paper elevation={14} sx={{ borderRadius: 2, overflow: 'hidden', boxShadow: 12 }}>
            {/* header */}
            <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', p: 1.25, borderBottom: 1, borderColor: 'divider' }}>
              <Box>
                <Typography variant="subtitle1">Jhania</Typography>
                <Typography variant="caption" color="text.secondary">Respuestas inmediatas</Typography>
              </Box>

              <Box sx={{ display: 'flex', gap: 1 }}>
                <Button size="small" onClick={clearChat}>Limpiar</Button>
                <IconButton onClick={() => setOpen(false)} size="small" aria-label="Cerrar chat">
                  <Close />
                </IconButton>
              </Box>
            </Box>

            {/* messages */}
            <Box
              ref={scrollRef}
              sx={{
                p: 1.5,
                height: chatHeight,
                overflowY: 'auto',
                bgcolor: 'background.default',
                display: 'flex',
                flexDirection: 'column',
                gap: 1,
              }}
            >
              {messages.map((m, idx) => (
                <Box
                  key={idx}
                  sx={{
                    display: 'flex',
                    justifyContent: m.from === 'user' ? 'flex-end' : 'flex-start',
                    px: 0.5,
                  }}
                >
                  <Paper
                    sx={{
                      p: 1.25,
                      maxWidth: '80%',
                      bgcolor: m.from === 'user' ? 'primary.main' : 'background.paper',
                      color: m.from === 'user' ? 'primary.contrastText' : 'text.primary',
                    }}
                    elevation={0}
                  >
                    <Typography variant="body2" sx={{ whiteSpace: 'pre-wrap' }}>{m.text}</Typography>
                  </Paper>
                </Box>
              ))}

              {/* streaming text while typing */}
              {botTyping && streamingText && (
                <Box sx={{ display: 'flex', justifyContent: 'flex-start', px: 0.5 }}>
                  <Paper sx={{ p: 1.25, maxWidth: '72%' }}>
                    <Typography variant="body2" sx={{ whiteSpace: 'pre-wrap' }}>{streamingText}</Typography>
                  </Paper>
                </Box>
              )}

              {/* typing indicator */}
              {botTyping && !streamingText && (
                <Box sx={{ display: 'flex', justifyContent: 'flex-start', px: 0.5 }}>
                  <Paper sx={{ p: 1.25, maxWidth: '40%' }}>
                    <Typography variant="body2" color="text.secondary">Escribiendo...</Typography>
                  </Paper>
                </Box>
              )}
            </Box>

            {/* quick actions + input */}
            <Box sx={{ p: 1, borderTop: 1, borderColor: 'divider' }}>
              <Box sx={{ display: 'flex', gap: 1, mb: 1, flexWrap: 'wrap' }}>
                <Button size="small" variant="outlined" onClick={() => quickAsk('Cuntos aos tiene Jhonatan?')}>Edad?</Button>
                <Button size="small" variant="outlined" onClick={() => quickAsk('Dnde vive Jhonatan?')}>Dnde vive?</Button>
                <Button size="small" variant="outlined" onClick={() => quickAsk('Proyectos de Jhonatan?')}>Proyectos</Button>
              </Box>

              <Box component="form" onSubmit={(e) => { e.preventDefault(); send(input); }} sx={{ display: 'flex', gap: 1 }}>
                <TextField
                  placeholder="Escribe tu pregunta..."
                  value={input}
                  onChange={(e) => setInput(e.target.value)}
                  size="small"
                  fullWidth
                  inputRef={inputRef}
                  InputProps={{
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton type="submit" aria-label="enviar" disabled={!input.trim()}>
                          <Send />
                        </IconButton>
                      </InputAdornment>
                    ),
                    'aria-label': 'campo de mensaje',
                  }}
                />
                <Button variant="contained" onClick={() => send(input)} disabled={!input.trim()}>Enviar</Button>
              </Box>
            </Box>
          </Paper>
        </Box>
      )}
    </>
  );
}
